home-container {
    text-align: center;
    padding: 20px;
    }

    .hero-section {
    background: url("https://store.storeimages.cdn-apple.com/1/as-images.apple.com/is/iphone-16-pro-model-unselect-gallery-1-202409?wid=5120&hei=2880&fmt=webp&qlt=70&.v=aWs5czA5aDFXU0FlMGFGRlpYRXk2UWFRQXQ2R0JQTk5udUZxTkR3ZVlpTEJnOG9obkp6NERCS3lnVm1tcnlVUjBoUVhuTWlrY2hIK090ZGZZbk9HeE1xUVVnSHY5eU9CcGxDMkFhalkvT0NuWUpOMGpEMHVTZEtYYVA3c1B3UzVmbW94YnYxc1YvNXZ4emJGL0IxNFp3&traceId=1") center/cover no-repeat;
    min-height: 220px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    text-align: center;
    padding: 100px 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    }
    .hero-content h2 {
    font-size: 36px;
    margin-bottom: 10px;
    }
    .hero-content p {
    font-size: 18px;
    margin-bottom: 20px;
    }
    .shop-now-btn {
    display: inline-block;
    background-color: #ff6f00;
    color: white;
    padding: 12px 20px;
    border-radius: 5px;
    text-decoration: none;
    font-size: 16px;
    font-weight: bold;
    transition: background 0.3s ease-in-out;
    }
    .shop-now-btn:hover {
    background-color: #e65c00;
    }
    .featured-products {
    margin-top: 50px;
    }
    .featured-products h3 {
    font-size: 24px;
    margin-bottom: 20px;
    color: white;
    }
    .product-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    justify-content: center;
    padding: 10px;
    }
    .product-card {
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 15px;
    transition: transform 0.3s ease-in-out;
    text-align: center;
    }
    .product-card:hover {
    transform: translateY(-5px);
    }
    .product-image {
    width: 100%;
    height: auto;
    border-radius: 10px;
    }
    .product-price {
    font-size: 16px;
    color: #27ae60;
    font-weight: bold;
    margin: 5px 0;
    }
    .product-description {
    font-size: 14px;
    color: #666;
    margin-bottom: 10px;
    }
    .view-details-btn {
    display: inline-block;
    background-color: #222;
    color: white;
    padding: 8px 15px;
    border-radius: 5px;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
    transition: background 0.3s ease-in-out;
    }
    .view-details-btn:hover {
    background-color: #444;
    }
    .add-product-container {
    max-width: 50%;
    margin: 40px auto;
    padding: 20px;
    background: #606062;
    border-color: #d55155;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    }
    .add-product-container h2 {
    color: white;
    font-weight: bolder;
    margin-bottom: 20px;
    }
    .add-product-container input {
    width: 25%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid wheat;
    border-radius: 5px;
    font-size: 16px;
    }
    .add-product-container button {
    background: #ff5722;
    color: wheat;
    padding: 12px;
    border: none;
    width: 200px;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
    transition: 0.3s ease;
    }
    .add-product-container button:hover {
    background: #e64a19;
    }
    .product-grid {
    display: flex;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    margin-top: 20px;
    }
    .cart-container {
    width: 600px;
    height: 100%;
    margin: 20px auto;
    padding: 20px;
    background: linear-gradient(135deg, #434140, #666262);
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    font-family: 'Arial', sans-serif;
    }
    .cart-container h2 {
    color: white;
    margin-bottom: 15px;
    font-size: 1.8rem;
    }
    .cart-list {
    list-style: none;
    padding: 0;
    }
    .cart-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    padding: 10px 15px;
    margin: 10px 0;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease-in-out;
    width: 30px;
    height: 100px;
    }
    .cart-item img{
    width: 150px;
    height: 120px;
    }
    .cart-item:hover {
    transform: scale(1.02);
    }
    .cart-item button {
    background: #ff5722;
    color: #fff;
    border: none;
    padding: 6px 12px;
    font-size: 0.9rem;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s ease-in-out;
    }
    .cart-item button:hover {
    background: #ff5722;
    }
    .cart-container p {
    font-size: 1.2rem;
    color:bla;
    margin-top: 10px;
    }
    .view-product-container {
    width: 90%;
    max-width: 1200px;
    margin: 30px auto;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    }
    .view-product-container h2 {
    font-size: 2rem;
    color: white;
    margin-bottom: 20px;
    }
    .no-products {
    font-size: 1.2rem;
    color: #777;
    }
    .product-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    justify-content: center;
    }
    .product-card {
    background: #fff;
    padding: 15px;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    text-align: center;
    }
    .product-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
    }
    .product-image {
    width: 100%;
    max-height: 160px;
    object-fit: cover;
    border-radius: 8px;
    margin-bottom: 10px;
    }
    .product-name {
    font-size: 1.2rem;
    color: #333;
    margin: 10px 0;
    }
    .product-price {
    font-size: 1rem;
    font-weight: bold;
    color: #ff5722;
    }
    .add-to-cart-btn {
    background: linear-gradient(135deg, #ff5722, #ff5722);
    color: white;
    border: none;
    padding: 8px 12px;
    font-size: 1rem;
    border-radius: 6px;
    cursor: pointer;
    transition: background 0.3s ease-in-out;
    }
    .add-to-cart-btn:hover {
    background: linear-gradient(135deg, #ff5722, #ff5722);
    }
    